.wp-block-create-block-article-box {
	background-color: white;
	color: black;
	padding: 10px;
	border: 1px groove black;
	border-radius: 5px;

	.heading {
		font-size: 47.8px;
		letter-spacing: 0.01em;
		font-family: 'Libre Baskerville', 'Times New Roman', serif;
		font-weight: 700;
	}

	.content {
		margin-top: -2em;
		font-family: 'Source Sans Pro';
		font-size: 27.65px;
		font-weight: 300;

	}
}